import React, { useState, useEffect } from 'react'
import Numbers from './components/Numbers'
import AddPersonForm from './components/AddPersonForm'
import FilterSearch from './components/FilterSearch'
import personServices from './services/persons'
import Notification from './components/Notification'

const App = () => {
  // STATE
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [search, setSearch] = useState('')
  const [notification, setNotification] = useState({
    type: null,
    message: null
  })

  const effectHook = () => {
    console.log('effect start')
    personServices
      .getAll()
      .then(data => {
        const personsFromJsonServer = data
        console.log(personsFromJsonServer)
        setPersons(personsFromJsonServer)
      })
  }

  // empty array -> only call effect after first render
  useEffect(effectHook, [])


  // EVENT HANDLERS

  const updateNameInput = (event) => {
    event.preventDefault()

    setNewName(event.target.value)
  }

  const updateNumberInput = (event) => {
    event.preventDefault()

    setNewNumber(event.target.value)
  }

  const handleAdd = (event) => {
    event.preventDefault()

    isPersonExistAlready(newName)
      ? askToUpdateNumber()
      // update persons state
      : addCurPersonToPhonebook()
  }

  // Helper Functions

  const askToUpdateNumber = () => {
    const isOK = window.confirm(`"${newName}" exists in the phonebook already. Would you like to replace the old number with a new one?`)
    if (isOK) {
      const personToUpdate = persons.find( p => p.name === newName)
      const indexToUpdate = persons.findIndex(person => personToUpdate === person)
      const copyPerson = {...personToUpdate, number: newNumber}

      personServices
        .update(personToUpdate.id, copyPerson)
        .then( data => {
          const copyOfPersons = copyPersons()
          copyOfPersons.splice(indexToUpdate, 1, data)
          setPersons(copyOfPersons)
          setNotificationHelper('success', `${newName}'s number was updated`, 5000)
        })
        .catch( error => {
          setNotificationHelper('error', `${newName}'s number was not updated`, 5000)
        })
      resetInputFields()
    }
  }

  const isPersonExistAlready = (name) => {
    return (persons.findIndex((person) => person.name === name) >= 0)
  }

  const addCurPersonToPhonebook = () => {
    const newPerson = {
      name: newName,
      number: newNumber
    }

    // create new person on server
    personServices
      .create(newPerson)
      .then( data => {
        // this is necessary to get the returned object from the server which has the generated id
        setPersons(persons.concat(data))
      })
      .then( data => {
        setNotificationHelper('success', `Added ${newName}`, 5000)
      })
      .catch( error => {
        const errorMsg = error.response.data.error
        setNotificationHelper('error', `${newName} was not added (error: ${errorMsg})`, 5000)
      })
    // clear input fields
    resetInputFields()
  }

  // set notification for type, message, timeout
  const setNotificationHelper = (type, message, timeout) => {
    setNotification(
      {
      type: type,
      message: message
      }
    )
    setTimeout(() => {
      setNotification({
        type: null,
        message: null})
    }, timeout)
  }

  const resetInputFields = () => {
    setNewName('')
    setNewNumber('')
  }

  const updateSearchInput = (event) => {
    event.preventDefault()

    const updatedSearchText = event.target.value
    setSearch(updatedSearchText)
  }

  // filters by searching for names containing the search phrase
  /* note: all strings contain the empty string
                so we can pass in the filteredPersons since an empty
                search bar will return all persons */
  const filteredPersons = () => {
    return persons.filter((person) => person.name.toLowerCase().includes(search.toLowerCase()))
  }

  const handleDelete = (id) => {
    return (event) => {
      event.preventDefault()

      // find index of person to delete
      const indexToDel = persons.findIndex(person => {
        return person.id === id
      })

      personServices.del(id)

      const copyOfPersons = copyPersons()
      copyOfPersons.splice(indexToDel, 1)
      setPersons(copyOfPersons)
    }
  }

  // deep copy
  const copyPersons = () => {
    return persons.map( person => {
      return {
        name: person.name,
        number: person.number,
        id: person.id
      }
    })
  }

  return (
    <>
      <h1>Phonebook</h1>

      <Notification type={notification.type} message={notification.message}/>

      <FilterSearch 
        search={search}
        updateSearchInput={updateSearchInput}
      />

      <AddPersonForm 
        headerTitle='Add New Person' 
        newName={newName}
        updateNameInput={updateNameInput}
        newNumber={newNumber}
        updateNumberInput={updateNumberInput}
        handleAdd={handleAdd}
         />

      {/* note: all strings contain the empty string
                so we can pass in the filteredPersons since an empty
                search bar will return all persons */}
      <Numbers 
        headerTitle='Numbers' 
        persons={filteredPersons()} 
        handleDelete={handleDelete} 
        />
    </>
  )
}

export default App