import React from 'react'


const Notification = ({type, message}) => {
    const successStyle = {
        color: 'green',
        background: 'lightgrey',
        fontSize: 20,
        borderStyle: 'solid',
        borderRadius: 5,
        padding: 10,
        marginBottom: 10
    }

    const errorStyle = {
        color: 'red',
        background: 'lightgrey',
        fontSize: 20,
        borderStyle: 'solid',
        borderRadius: 5,
        padding: 10,
        marginBottom: 10
    }


    if (type === null) {
        return null
    } else {
        return (
            <div style={(
                type === 'success' ? 
                successStyle : 
                errorStyle)}>

                {message}

            </div>
        )
    }
}

export default Notification