import React from 'react'


const Numbers = ({ headerTitle, persons, handleDelete }) => {
    return (
      <div>
        <h2>{headerTitle}</h2>
  
        {persons.map(person => {
          return (
            <div key={person.id}>
              <span>{person.name} {person.number}</span>
              <button onClick={handleDelete(person.id)} type='button'>delete</button>
              <br></br>
            </div>
          )
          
        }
        )}
      </div>
    )
  }

export default Numbers