import React from 'react'


const AddPersonForm = (props) => {
    const {
        headerTitle,
        newName, updateNameInput,
        newNumber, updateNumberInput,
        handleAdd
    } = props


    return (
        <>
            <h2>{headerTitle}</h2>

            <form>
                <div>
                    name: <input value={newName} onChange={updateNameInput} />
                </div>

                <div>
                    number: <input value={newNumber} onChange={updateNumberInput} />
                </div>

                <div>
                    <button onClick={handleAdd} type="submit">add</button>
                </div>
            </form>
        </>
    )
}

export default AddPersonForm